.currentWeekContainer {
  display: flex;
  align-items: center;
  margin-top: 30px;
}

.currentWeek {
  font-size: 20px;
  margin-right: 10px;
  margin-left: 10px;
}

.arrows {
  button {
    border: none;
    border-radius: 50%;
    height: 25px;
    width: 25px;
    background-color: #f0ad4e;
    padding: 0;

    &:active {
      background-color: #d4d4d4;
    }

    svg {
      width: 24px;
      height: 24px;
      fill: #4a4a4a;
    }
  }

  .right {
    margin-left: 10px;

    &:disabled {
      cursor: default;
      opacity: 0.6;
    }
  }
}

.slotColorInfo {
  display: flex;
  margin: 16px 0 10px;

  > * {
    margin: 4px 8px;
    display: flex;

    span {
      display: inline-block;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      margin-right: 5px;
    }
  }

  .weeklySlot {
    span {
      background-color: #cef2dd;
      border: 1px solid #e0e0e0;
    }
  }

  .singleSlot {
    span {
      background-color: #cff4ff;
      border: 1px solid #e0e0e0;
    }
  }

  .justInTime {
    span {
      background-color: #fabc29;
      display: inline-flex;
    }
  }

  .priorityTime {
    span {
      background-color: #ffd9c5;
      border: 1px solid #e0e0e0;
    }
  }

  .halfPriorityTime {
    span {
      background-color: #fff6df;
      border: 1px solid #e0e0e0;
    }
  }
}

.divider {
  height: 18px;
  border-left: 1px solid #4a4a4a;
}

.slotHeader,
.slots {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;

  th,
  td {
    border: 1px solid #e0e0e0;
    width: 12.5%;
    height: 38px;
    padding: 2px 12px;

    &:first-child {
      text-align: center;
    }
  }

  td {
    cursor: pointer;
  }
}

.slots {
  tr:first-child {
    td:not(.selected) {
      border-top: none;
    }
  }
}

.slot {
  position: relative;

  &.selected {
    border: 1px double !important;
    border-color: #00adcb !important;
  }

  &.disabled {
    cursor: default;
    border-color: #ccc;

    &::after {
      display: block;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(20, 20, 20, 0.1);
    }
  }

  &.single {
    background-color: #cff4ff !important;
  }

  &.weekly {
    background-color: #cef2dd !important;
  }

  &.halfHotWeekEnd,
  &.halfHot {
    background-color: rgba(255, 246, 223, 0.6);
  }

  .badStatus::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    border-left: 8px solid transparent;
    border-top: 8px solid red;
    border-bottom: 8px solid transparent;
  }

  &:hover {
    .wrong {
      display: block;
    }
  }

  .wrong {
    display: none;
    position: absolute;
    margin-top: -100%;
    left: 0;
    background: #fff;
    width: 100%;
  }
}

td.slot.hot {
  background-color: rgba(255, 217, 197, 0.6);
}

.slotInfo {
  display: flex;
  align-items: center;
}

.jitStatus {
  width: 16px;
  height: 16px;
  border: 2px solid #fabc29;
  border-radius: 50%;
  margin-right: 12px;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  &.active {
    background-color: #fabc29;
  }
}

.trial {
  margin-left: auto;
  flex-shrink: 0;
}

.studentId {
  color: #000;
  line-height: 14px;
  margin: 4px 0 2px;
  font-weight: 700;
  font-size: 14px;
}

.studentName {
  color: #4a4a4a;
  font-weight: 300;
  font-size: 11px;
  line-height: 12px;
  height: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.actionPopup {
  position: absolute;
  right: -16px;
  top: 18px;
  display: flex;
  flex-direction: column;
  padding: 15px;
  border: 1px solid #e5e5ea;
  box-sizing: border-box;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  border-radius: 7px;
  background-color: #fff;
  width: 256px;
  z-index: 2;
  transform: translate(100%, -50%);
  cursor: default;

  .date {
    margin-bottom: 12px;
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
  }

  .lessonName {
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 16px;
    color: #4a4a4a;
  }

  .popupSlotInfo {
    color: #eb5757;
    font-style: normal;
    font-size: 15px;
    line-height: 20px;

    span {
      font-weight: 700;
    }
  }

  .additionalStatuses {
    margin: 16px 0;
  }

  button {
    margin: 10px 0;
    font-size: 13px;
    font-weight: 700;
    outline: 0 !important;
    text-align: center;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    padding: 6px 12px;
    line-height: 1.42857;
    border-radius: 50px;
    width: 100%;
  }

  .jit {
    cursor: pointer;
  }

  .create {
    button {
      color: #4a4a4a;
      background-color: #fbc33e;
      border-color: #fab30c;

      &:hover {
        background-color: #fab30c;
        border-color: #845e03;
      }
    }
  }

  .delete {
    button {
      color: #fff;
      background-color: #ff596e;
      border-color: #ff2641;

      &:hover {
        background-color: #ff2641;
        border-color: #e8001d;
      }
    }
  }

  .jit {
    display: inline-flex;
    align-items: center;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: #42aac8;
    margin-top: 12px;
    margin-left: 4px;
  }

  &::after,
  &::before {
    right: 100%;
    top: 50%;
    border: solid transparent;
    content: "";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
  }

  &::after {
    border-color: rgba(136, 183, 213, 0);
    border-right-color: #fff;
    border-width: 14px;
    margin-top: -14px;
  }

  &::before {
    border-color: rgba(229, 229, 234, 0);
    border-right-color: #e5e5ea;
    border-width: 15px;
    margin-top: -15px;
  }
}
